<template>
  <nav v-if="stop" class="navbar network-sub-navbar navbar-light nav-top">
    <div class="container-fluid">
      <router-link class="sub-link" v-for="node in stop.nodes" v-bind:to="{ name: 'MapPage2', params: { stop: stop.href, mappage: node.href}}" v-bind:key="node.id" v-bind:id="node.href">
        {{node.label}}
        </router-link>
        <router-view></router-view>
    </div>
  </nav>
</template>

<script>

export default {
  name: 'TopNavbarNetwork',
  props: {
    stop:Object
  },
  mounted(){
    this.setLinkHover();
  },
  updated(){
    this.setLinkHover();
  },
   watch:{
      '$route':'setLinkHover'
  },
  methods:{
    setLinkHover(){        
        var color = this.stop.color,
            page = this.$route.params.mappage;

        document.querySelectorAll(".network-sub-navbar").forEach(function(nav){
          nav.style.backgroundColor = color;
        })

        this.stop.nodes.forEach(function(node){
            if (page == node.href){
                document.querySelector("#" + node.href).style.backgroundColor = "rgb(250,250,250)";
                document.querySelector("#" + node.href).style.color = "black";
            }
            else{
                document.querySelector("#" + node.href).style.backgroundColor = "";
                document.querySelector("#" + node.href).style.color = "white";
            }
        })
    }
  }
}
</script>

<style>
    .network-sub-navbar{
        border-bottom:rgb(250,250,250) solid 4px;
    }
    .sub-link{
        color:white;
        font-size:18px;
    }
    .sub-link:hover{
        color:black !important;
        background-color:rgb(250,250,250) !important;
    }
</style>
