import { createRouter, createWebHashHistory } from 'vue-router'
import NexusPage from '../NexusPage.vue'
import HomePage1 from '../HomePage1.vue'
import HomePage2 from '../HomePage2.vue'
import MapPage1 from '../MapPage1.vue'
import MapPage2 from '../MapPage2.vue'

const routes = [
  {
    path: '/',
    name: 'NexusPage',
    component: NexusPage
  },
  {
    path: '/home',
    name: 'HomePage1',
    component: HomePage1
  },
  {
    path: '/homepage',
    name: 'HomePage2',
    component: HomePage2
  },
  {
    path: '/:mappage',
    name: 'MapPage1',
    component: MapPage1
  },
  {
    path: '/:stop/:mappage',
    name: 'MapPage2',
    component: MapPage2
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if(savedPosition){
        return savedPosition
    }
    else{
      return { 
        top: 0 
      }
    }
  }
})

export default router
