<script>

import LeafletMap from './LeafletMap.vue'
import SideBySide from './SideBySide.vue'

export default {
  name: 'ScrollMap',
  components: {
     LeafletMap,
     SideBySide
  },
  props:{
    title:String,
    titleImg:String,
    titleType:String,
    content:Array,
    color:String,
    background:String
  },
  created(){
    let self = this;
    window.addEventListener('resize',function(){
        self.resizeBackround();
    })
  },
  mounted(){
    this.setColor();
    this.resizeBackround();
  },
  updated(){
    this.setColor();
    this.resizeBackround();
  },
  methods:{
    setColor(){
        let self = this;
        document.querySelectorAll("b, hr, .network-link").forEach(function(elem){
            elem.style.color = self.color;
        })
    },
    resizeBackround(){
        var width = document.documentElement.clientWidth;

        document.querySelectorAll('.container').forEach(div =>{
            div.width = width + 'px !important';
        });
    }
  }

}
</script>

