<template>
  <TopNavbarNetwork v-bind:links="links"></TopNavbarNetwork>
  <SubNavbar v-if="stop" v-bind:stop="stop"></SubNavbar>
  <div id="mappage" v-if="content" v-bind:style=" 'border-left: 20px solid' + color">
    <ScrollMap v-bind:content="content.content" v-bind:color="color" v-bind:title="content.name" v-bind:titleType="content.titleType" v-bind:titleImg="content.titleImg" v-bind:background="content.background"></ScrollMap>
  </div>
  <SubNavbar v-if="stop" v-bind:stop="stop"></SubNavbar>
  <!--<BottomNavbarNetwork v-if="content" v-bind:color="color" v-bind:links="links"></BottomNavbarNetwork>-->
</template>

<script>

import TopNavbarNetwork from './components/TopNavbarNetwork.vue'
import SubNavbar from './components/SubNavbar.vue'
//import BottomNavbarNetwork from './components/BottomNavbarNetwork.vue'
import ScrollMap from './components/ScrollMap.vue'

export default {
  name: 'MapPage2',
  inject:["links"],
  components: {
    TopNavbarNetwork,
    ScrollMap,
    SubNavbar
  },
  data(){
    return{
      content:null,
      color:null,
      stop:null
    }
  },
  created(){
      this.fetchData();
  },
  updated(){
      this.selectColor();
  },
  watch:{
      '$route':'fetchData'
  },
  methods:{
    fetchData(){
        if (this.$route.params.mappage){
          let self = this,
            links = self.links;

          links.forEach(function(link){
              if (link.href == self.$route.params.stop){
                  self.stop = link;
              }
          })

          var fileName = "./pages/" + this.$route.params.stop + "/" + this.$route.params.mappage + ".json";
          const myRequest = new Request(fileName);

          fetch(myRequest)
            .then( res => res.json())
            .then(data => {
                self.content = data;
                self.selectColor();
              })
            .catch(function(error) {
              console.log('Request failed', error)
            });
        }
     },
     selectColor(){
       let self = this,
           page = self.$route.params.stop;

       self.links.forEach(function(link){
         if (link.href == page){
            self.color = link.color;
            self.setColorScheme(self.color);
         }
       })
     },
     setColorScheme(color){
        var stop = this.$route.params.stop;
        
        document.querySelectorAll(".link").forEach(function(link){
           link.style.color = "black";
           link.style.backgroundColor = "rgba(0,0,0,0)";
        });
        document.querySelector("#" + stop).style.color = "white";
        document.querySelector("#" + stop).style.backgroundColor = color;
     }
  }
}
</script>

